import { Component, ElementRef, ViewChild } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { CommonModule } from '@angular/common';
import { FormBuilder, FormGroup, FormsModule, ReactiveFormsModule, Validators } from '@angular/forms';
import { DomSanitizer,SafeResourceUrl  } from '@angular/platform-browser';
declare var bootstrap: any;
interface Blog {
  id: number;
  title: string;
  date: string;
  time: string;
  venue?: string | null;
  announcements?: string | null;
  mediaFile?: string | null;
  createdAt: string;
  updatedAt: string;
  isActive: boolean;
}

@Component({
  selector: 'app-mo-m',
  standalone: true,
  imports: [CommonModule, FormsModule, ReactiveFormsModule],
  templateUrl: './mo-m.component.html',
  styleUrls: ['./mo-m.component.scss']
})
export class MoMComponent {
  blogForm: FormGroup;
  previewUrl:any = null;
  searchText: string = '';
  pageSize: number = 5;
  currentPage: number = 1;
  totalPages: number = 1;
  toastMessage: string = '';
  blogs: Blog[] = [];
  editEventForm!: FormGroup;
  selectedEventId: number | null = null;
  @ViewChild('errorToast', { static: false }) errorToast!: ElementRef;
  today: string = new Date().toISOString().split('T')[0];
  base64Image: string | null = null;
  uploadedFileName: string | null = null;
  isImageFile: boolean = false;
  isPdfFile: boolean = false;
  safeUrl!: SafeResourceUrl;
fileRequired: boolean = false;

  @ViewChild('fileUploadInput') fileUploadInput!: ElementRef<HTMLInputElement>;

constructor(private fb: FormBuilder, private http: HttpClient, private sanitizer: DomSanitizer) {
  this.blogForm = this.fb.group({
    title: ['', Validators.required],
    date: ['', Validators.required],
    time: ['', Validators.required],
    announcements: [''],
    notifyAll: [false],
    shareWithExecutive: [false],
    bannerImage: [null] // file input
  });

  // By default, "Meeting Notice" is selected -> file not required
  this.fileRequired = false;

  // Optional: If you want to subscribe to changes later
  // Here we just set default behavior, radio change handled by onHeadingOptionChange()
}


  ngOnInit(): void {
    this.loadBlogs();
    this.editEventForm = this.fb.group({
      title: ['', [Validators.required]],
      // description: ['', [Validators.required]]
    });
        this.safeUrl = this.sanitizer.bypassSecurityTrustResourceUrl(this.previewUrl);

  }

  openEditDrawer(blog: Blog): void {
    this.selectedEventId = blog.id;
    this.editEventForm.patchValue({
      title: blog.title,
      // description: blog.description
    });

    const offcanvasElement = document.getElementById('editEventDrawer');
    if (offcanvasElement) {
      const bsOffcanvas = new bootstrap.Offcanvas(offcanvasElement);
      bsOffcanvas.show();
    }
  }

  onHeadingOptionChange(option: 'text' | 'image') {
  const fileControl = this.blogForm.get('bannerImage');
  if (option === 'image') {
    this.fileRequired = true;
    fileControl?.setValidators([Validators.required]);
  } else {
    this.fileRequired = false;
    fileControl?.clearValidators();
  }
  fileControl?.updateValueAndValidity();
}

  onUpdateEvent(): void {
    if (this.editEventForm.invalid || !this.selectedEventId) return;

    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });

    // âœ… PATCH MoM endpoint
    this.http.patch(
      `${environment.apiUrl}/mom/${this.selectedEventId}`,
      this.editEventForm.value,
      { headers }
    ).subscribe({
      next: (res) => {
        console.log('MoM updated:', res);
        this.showToast('MoM updated successfully!', 'success');
        this.loadBlogs();
        const offcanvasEl = document.getElementById('editEventDrawer');
        if (offcanvasEl) {
          const bsOffcanvas = bootstrap.Offcanvas.getOrCreateInstance(offcanvasEl);
          bsOffcanvas.hide();
        }
      },
      error: (err) => {
        console.error('Failed to update MoM:', err);
        this.showToast('Failed to update MoM', 'danger');
      }
    });
  }

  loadBlogs(): void {
    const token = localStorage.getItem('authToken');
    if (!token) {
      this.showToast('Auth token not found', 'danger');
      return;
    }

    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`,
      'Content-Type': 'application/json'
    });

    // âœ… GET MoM endpoint
    this.http.get<any>(`${environment.apiUrl}/mom?page=${this.currentPage}&limit=${this.pageSize}&isActive=true`, { headers })
      .subscribe({
        next: (res) => {
          this.blogs = res.data.data;
          this.totalPages = res.data.pagination?.total
            ? Math.ceil(res.data.pagination.total / this.pageSize)
            : 1;
        },
        error: (err) => {
          console.error(err);
          this.showToast('Failed to load MoM details', 'danger');
        }
      });
  }


 onFileSelected(event: any): void {
  const file = event.target.files?.[0];
  if (!file) return;

  if (file.size > 5 * 1024 * 1024) {
    this.showToast('File size exceeds 5MB. Please upload a smaller file.', 'danger');
    return;
  }

  this.uploadedFileName = file.name;
  this.isImageFile = file.type.startsWith('image/');
  this.isPdfFile = file.type.includes('pdf');

  const reader = new FileReader();
  reader.onload = () => {
    // Convert file to base64 string
    const base64String = reader.result as string;

    // Bind base64 string to your form control
    this.blogForm.patchValue({ bannerImage: base64String });

    // Optionally show preview in iframe or img
    this.previewUrl = this.sanitizer.bypassSecurityTrustResourceUrl(base64String);
  };
  reader.readAsDataURL(file);
}


  openImagePreviewModal(): void {
    const modalElement = document.getElementById('imagePreviewModal');
    if (modalElement) {
      const modal = new bootstrap.Modal(modalElement);
      modal.show();
    }
  }

  deleteFile(): void {
    this.previewUrl = null;
    this.base64Image = null;
    this.uploadedFileName = null;
    this.isImageFile = false;
    this.isPdfFile = false;
    this.blogForm.patchValue({ bannerImage: null });
    this.blogForm.get('bannerImage')?.updateValueAndValidity();

    if (this.fileUploadInput?.nativeElement) {
      this.fileUploadInput.nativeElement.value = '';
    }
  }

  changePage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.currentPage = page;
      this.loadBlogs();
    }
  }

  goPrevious() {
    if (this.currentPage > 1) {
      this.currentPage--;
      this.loadBlogs();
    }
  }

  goNext() {
    if (this.currentPage < this.totalPages) {
      this.currentPage++;
      this.loadBlogs();
    }
  }

  onPageSizeChange(event: any) {
    this.pageSize = +event.target.value;
    this.currentPage = 1;
    this.loadBlogs();
  }

  get filteredBlogs(): Blog[] {
    const term = this.searchText.toLowerCase();
    return this.blogs.filter(blog =>
      blog.title.toLowerCase().includes(term) ||
      blog.createdAt.toLowerCase().includes(term) ||
      blog.updatedAt.toLowerCase().includes(term)
    );
  }

onSubmit() {
  if (this.blogForm.invalid) {
    this.showToast('Please fill all required fields', 'danger');
    return;
  }

  const token = localStorage.getItem('authToken');
  if (!token) {
    this.showToast('Auth token not found', 'danger');
    return;
  }

  const headers = new HttpHeaders({
    'Authorization': `Bearer ${token}`,
    'Content-Type': 'application/json'
  });

  const payload = {
    title: this.blogForm.value.title,
    date: this.blogForm.value.date,
    time: this.blogForm.value.time,
    announcement: this.blogForm.value.announcements?.trim() ? this.blogForm.value.announcements : null,
    shareWithAll: this.blogForm.value.notifyAll ? true : false,
    shareWithExecutive: this.blogForm.value.shareWithExecutive ? true : false,
    mediaFile: this.blogForm.value.bannerImage
  };

  // âœ… POST MoM endpoint
  this.http.post(`${environment.apiUrl}/mom`, payload, { headers })
    .subscribe({
      next: () => {
        this.showToast('MoM submitted successfully!', 'success');

        // Reset form
        this.blogForm.reset();
        this.blogForm.get('bannerImage')?.setValue(null);

        // Clear uploaded file preview
        this.previewUrl = null;
        this.uploadedFileName = null;
        this.isImageFile = false;
        this.isPdfFile = false;

        // Clear native file input if exists
        if (this.fileUploadInput?.nativeElement) {
          this.fileUploadInput.nativeElement.value = '';
        }

        this.loadBlogs();
      },
      error: (err) => {
        if (err.error && err.error.errors && Array.isArray(err.error.errors)) {
          err.error.errors.forEach((e: any) => {
            this.showToast(e.message, 'danger');
          });
        } else if (err.error && err.error.message) {
          this.showToast(err.error.message, 'danger');
        } else {
          this.showToast('Failed to submit MoM', 'danger');
        }
      }
    });
}


openMediaFile(base64Data: string, fileName: string = 'file') {
  if (!base64Data) return;

  // Detect file type
  const isImage = base64Data.startsWith('data:image');
  const isPdf = base64Data.startsWith('data:application/pdf') || base64Data.includes('pdf');

  if (isImage) {
    // For images, open in new tab directly
    const newTab = window.open();
    if (newTab) {
      newTab.document.write(`<title>${fileName}</title>`);
      newTab.document.write(`<img src="${base64Data}" style="max-width:100%; height:auto;" />`);
      newTab.document.close();
    }
  } else if (isPdf) {
    // For PDFs, create a Blob and open in new tab
    const byteString = atob(base64Data.split(',')[1]);
    const arrayBuffer = new ArrayBuffer(byteString.length);
    const intArray = new Uint8Array(arrayBuffer);
    for (let i = 0; i < byteString.length; i++) {
      intArray[i] = byteString.charCodeAt(i);
    }

    const blob = new Blob([intArray], { type: 'application/pdf' });
    const blobUrl = URL.createObjectURL(blob);

    const newTab = window.open(blobUrl, '_blank');
    if (newTab) {
      newTab.document.title = fileName; // set tab title
    }
  } else {
    console.warn('Unsupported file type');
  }
}





  closeOffcanvas(id: string) {
    const el = document.getElementById(id);
    if (el) {
      const instance = bootstrap.Offcanvas.getInstance(el) || new bootstrap.Offcanvas(el);
      instance.hide();
    }
  }

  onDeleteBlog(userId: number) {
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({ Authorization: `Bearer ${token}` });
    const payload = { isActive: false };

    // âœ… PATCH MoM endpoint for delete
    this.http.delete(`${environment.apiUrl}/mom/${userId}`)
      .subscribe({
        next: () => {
          this.showToast('MoM deleted successfully!', 'success');
          this.loadBlogs();
        },
        error: (err) => {
          console.error('Failed to delete MoM', err);
          this.showToast('Failed to delete MoM', 'danger');
        }
      });
  }

  private showToast(message: string, type: 'success' | 'danger') {
    this.toastMessage = message;
    const toastElement = this.errorToast.nativeElement;
    toastElement.classList.remove('bg-success', 'bg-danger');
    toastElement.classList.add(type === 'success' ? 'bg-success' : 'bg-danger');
    new bootstrap.Toast(toastElement).show();
  }
}
